import "./styles.css";
import nyc from "./nyc.jpg";
import sf from "./sf.jpg";
import ithaca from "./ithaca.jpg";
import react, { useState, useReducer } from "react";

function Header(props) {
  return (
    <header>
      <h1>Hello {props.name}, here are cities!</h1>
    </header>
  );
}

function Main(props) {
  return (
    <section>
      <img
        src="https://github.com/avs7318.png"
        height={100}
        alt="profile of github user avs7318"
      />
      <p>This is a {props.adjective} world! Here are some cities: </p>
      <div>
        <img src={nyc} height={100} alt="wide shot of new york city" />
        <img src={sf} height={100} alt="wide shot of san francisco" />
        <img
          src={ithaca}
          height={100}
          width={150}
          alt="wide shot of ithaca, specifically cornell"
        />
      </div>
      <ul style={{ textAlign: "left" }}>
        {props.cities.map((city) => (
          <li key={city.id}>{city.title}</li>
        ))}
      </ul>
    </section>
  );
}

function Footer(props) {
  return (
    <footer>
      <p>{props.year}</p>
    </footer>
  );
}

const cities = ["New York City", "San Francisco", "Ithaca"];

const cityObjects = cities.map((city, i) => ({ id: i, title: city }));



function SecretComponent() {
  return <h1>Secret component only authorized users can see.</h1>;
}

export default function App(props) {
  const [select, setCity] = useState("New York City, NY");
  const [checked, toggle] = useReducer((checked) => !checked, false);

  if (props.authorized) {
    return <SecretComponent />;
  }
  return (
    <div className="App">
      <input type="checkbox" value={checked} onChange={toggle} />
      <p>{checked ? "checked" : "not checked"}</p>

      <Header name="Person" />
      <Main adjective="great" cities={cityObjects} />
      <>
        <h1>Currently you've selected: {select}.</h1>
        <button onClick={() => setCity("New York City, NY")}>
          New York City
        </button>
        <button onClick={() => setCity("San Francisco, CA")}>
          San Francisco
        </button>
        <button onClick={() => setCity("Ithaca, NY")}>Ithaca</button>
      </>
      <Footer year={new Date().getFullYear()} />
    </div>
  );
}
